.DashboardNav {
  width: 10rem;
  margin-left: -10rem;
  transition: margin-left .6s ease;
}

.DashboardNav.open {
  margin-left: 0;
}

.DashboardNav * {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.DashboardNav__content {
    padding: 0 1.625rem;
  }

.DashboardNav__item {
    height: 7.5rem;
  }

.DashboardNav__item.grabbable {
    cursor: -webkit-grab;
    cursor: grab;
  }

.DashboardNav__item.grabbable:active {
    cursor: -webkit-grabbing;
    cursor: grabbing;
  }

.DashboardNav__check {
    transform: translate(50%, -50%);
  }

.DashboardItem.pl {
  border: dashed 2px #fff;
  background-color: transparent;
}

.DashboardItem__header {
    height: 20%;
  }

.full .DashboardItem__header {
      height: 10%;
    }

.DashboardContent__wrapper {
    width: 95%;
    height: 97%;
  }
  .DashboardContent__item {
    will-change: width, height, transform;
    transition-property: width, height, transform;
    transition: width .6s cubic-bezier(.68, -0.55, .265, 1.55), height .6s, transform .3s;
  }
  .DashboardContent__item.react-grid-item.cssTransforms {
    transition-property: width, height, transform;
  }
  .DashboardContent__item.react-grid-item.cssTransforms.placeholder {
    transition-property: transform;
  }
  .DashboardContent .react-grid-item.react-grid-placeholder {
    background-color: transparent;
  }

.Dashboard__header {
    height: 10%;
  }
  .Dashboard__toggle-nav:hover span {
      color: #1dcacb;
    }
  .Dashboard__toggle-nav span {
      color: white;
      transform: scaleX(1);
      transition: color .3s ease, transform .3s ease;
    }
  .Dashboard__toggle-navIcon.open {
      transform: scaleX(-1);
    }
  .Dashboard__main {
    height: 90%;
  }

